import React from "react"
import { Link } from "react-router-dom"
import { Button, Stack, Typography } from "@mui/material"
import { SSO_LOGIN_ROUTE } from "@rp/react/web/router"

const SSOSignInButton = ({ email }: { email?: string }) => {
  const params = new URLSearchParams()
  if (email) {
    params.set("email", email)
  }
  return (
    <Stack width="100%" sx={{ mt: 1 }}>
      <Link
        to={`${SSO_LOGIN_ROUTE}/?${params.toString()}`}
        style={{ textDecoration: "none", margin: 0 }}
      >
        <Button
          onClick={() => {}}
          variant="contained-light"
          fullWidth
          sx={theme => ({
            display: "flex",

            background: theme.palette.grey[800],
            transition: "background 0.3s",

            ":hover": { background: theme.palette.grey[700], textDecoration: "none" },
          })}
        >
          <Typography variant="body1" color="text.primary" textTransform="none">
            SSO Login
          </Typography>
        </Button>
      </Link>
    </Stack>
  )
}

export { SSOSignInButton }
